export default function addToFavorites(event) {
  const newsId = event.target.dataset.newsId;

  const favoriteList = JSON.parse(localStorage.getItem('favoriteList')) || []; //    localStorage
  const favoriteIndex = favoriteList.findIndex(
    favorite => favorite.id === newsId
  ); //    
  if (favoriteIndex === -1) {
    //      
    favoriteList.push({ id: newsId }); //   
    localStorage.setItem('favoriteList', JSON.stringify(favoriteList)); //    localStorage
    event.target.textContent = 'RemoveFromFavorite'; //   
  } else {
    //      
    favoriteList.splice(favoriteIndex, 1); //  
    localStorage.setItem('favoriteList', JSON.stringify(favoriteList)); //    localStorage
    event.target.textContent = 'AddToFavorite'; //   
  }
}

